import './Login.css'
import React, { useState } from 'react';
import {auth} from "./firebase"
import { useHistory } from "react-router-dom"
import firebase from 'firebase/app';
import "firebase/database"



function Register() {

    const history = useHistory()
    const[email,setEmail] = useState('');
    const[password, setPassword] = useState('')
    const[firstName, setfirstName] = useState('')
    const[lastName, setlastName] = useState('')
    const[phoneNumber, setphoneNumber] = useState('')
    const[adress, setadress] = useState('')
    
    


    const writeUserData = (userId,firstName,lastName, email, adress, phoneNumber) => {
        firebase.database().ref('users/' + userId.setEmail({
            Name:{firstName} + {lastName},
            email:{email},
            adress:{adress},
            phoneNumber:{phoneNumber}
        }))
    }
    console.log(lastName)


    const Mask = e => {
        var x = e.target.value.replace(/\D/g, '').match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
    e.target.value = !x[2] ? x[1] : '(' + x[1] + ') ' + x[2] + (x[3] ? '-' + x[3] : '')
      }   





    const register =  e => {
        e.preventDefault();
        writeUserData()
        auth
        .createUserWithEmailAndPassword(email,password)
        .then((auth) => {
            console.log(auth);
            if (auth) {
                history.push('/')
            }
        })
        .catch(error => alert(error.message))
    }





    return (
  <div className="register">
                    <h1>Register</h1>
                    <form className="register_form">
                        <h4>Name:</h4>
                            <input type="text" required placeholder="First Name" value={firstName} onChange={e => setfirstName(e.target.value)}/>
                            <input type="text" required placeholder="Last Name" value={lastName} onChange={e => setlastName(e.target.value)}/>
                        <h4>Phone Number</h4> 
                            <input type="text" placeholder="(555) 555-5555" onInput={e =>Mask(e)} required value={phoneNumber}></input>
                            <h4>E-mail:</h4>
                        <input type="email" value={email} onChange={e => setEmail(e.target.value)} required></input>
                        <h4>Password:</h4>            
                        <input type="password" value={password} onChange={e => setPassword(e.target.value)} required></input> 

                        <div class="form-group">
                            <input type="street" class="form-control" id="autocomplete" placeholder="Street"required/>
                            <input type="city" class="form-control" id="inputCity" placeholder="City" required/>
                            <input type="state" class="form-control" id="inputState" placeholder="State" required/>
                            <input type="zip" class="form-control" id="inputZip" placeholder="Zip" required/>
                            <input type="county" class="form-control" id="inputCounty" placeholder="County" required/>
                            <input type="country" class="form-control" id="inputCountry" placeholder="Country" required/>
                        </div>                     
    
                        <button className="register_button" onClick={register}>Register</button>
                    </form>
                    
                </div>  
    )
}

export default Register
